import React from 'react';
import { useAccount } from 'wagmi';
import { useWeb3Modal } from '@web3modal/wagmi/react';

interface AccountModalProps {
  isOpen: boolean;
  onClose: () => void;
  address: string;
  apeBalance: string;
  bitBalance: string;
}

const AccountModal: React.FC<AccountModalProps> = ({
  isOpen,
  onClose,
  address,
  apeBalance,
  bitBalance
}) => {
  const { disconnect } = useAccount();
  
  if (!isOpen) return null;
  
  // Format address for display (truncate middle)
  const displayAddress = `${address.substring(0, 10)}...${address.substring(address.length - 8)}`;
  
  const handleDisconnect = () => {
    disconnect();
    onClose();
  };
  
  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="modal-header">
          <h2 className="modal-title">ACCOUNT</h2>
          <button className="modal-close" onClick={onClose}>√ó</button>
        </div>
        
        <div className="bg-royal-blue-dark p-4 mb-4">
          <h3 className="pixel-text text-white mb-4">YOUR ACCOUNT INFORMATION AND BALANCES</h3>
          
          <div className="mb-6">
            <p className="pixel-text text-sm mb-2">WALLET ADDRESS</p>
            <div className="flex justify-between items-center bg-royal-blue p-2">
              <span className="pixel-text text-sm">{displayAddress}</span>
              <button 
                className="pixel-button text-xs"
                onClick={() => navigator.clipboard.writeText(address)}
              >
                COPY
              </button>
            </div>
          </div>
          
          <div className="mb-2">
            <p className="pixel-text text-sm">APE BALANCE</p>
            <p className="pixel-text text-banana-yellow">{apeBalance} APE</p>
          </div>
          
          <div className="mb-6">
            <p className="pixel-text text-sm">BIT BALANCE</p>
            <p className="pixel-text text-banana-yellow">{bitBalance} BIT</p>
          </div>
          
          <div className="bg-royal-blue p-4 mb-4">
            <p className="pixel-text text-sm mb-2">NEED TO BRIDGE APE TO APECHAIN?</p>
            <button className="pixel-button w-full flex justify-center items-center">
              BRIDGE APE <span className="ml-2">‚Üí</span>
            </button>
          </div>
        </div>
        
        <div className="flex justify-between">
          <button className="pixel-button" onClick={onClose}>
            CLOSE
          </button>
          <button className="bg-red-600 text-white px-4 py-2 rounded" onClick={handleDisconnect}>
            DISCONNECT
          </button>
        </div>
      </div>
    </div>
  );
};

export default AccountModal;
