import { useAccount, useReadContract, useWriteContract } from 'wagmi';
import { formatEther, parseEther } from 'viem';
import { MAIN_CONTRACT_ADDRESS, MAIN_CONTRACT_ABI, BITAPE_TOKEN_ADDRESS, BITAPE_TOKEN_ABI } from '../config/web3';
import { useEffect, useState } from 'react';

export interface GameState {
  // User state
  isConnected: boolean;
  address: string | undefined;
  
  // Resources
  apeBalance: string;
  bitBalance: string;
  spacesLeft: number;
  gigawattsAvailable: number;
  
  // Mining stats
  miningRate: string;
  hashRate: string;
  blocksUntilHalving: string;
  networkHashRatePercentage: string;
  totalNetworkHashRate: string;
  
  // Mining rewards
  minedBit: string;
  
  // Facility state
  hasFacility: boolean;
  facilityLevel: number;
  facilityPower: number;
  
  // Actions
  purchaseFacility: (referrer?: string) => Promise<void>;
  getStarterMiner: () => Promise<void>;
  claimReward: () => Promise<void>;
  upgradeFacility: () => Promise<void>;
  
  // Loading states
  isPurchasingFacility: boolean;
  isGettingStarterMiner: boolean;
  isClaimingReward: boolean;
  isUpgradingFacility: boolean;
}

export function useGameState(): GameState {
  const { address, isConnected } = useAccount();
  
  // State variables
  const [apeBalance, setApeBalance] = useState('0');
  const [bitBalance, setBitBalance] = useState('0');
  const [minedBit, setMinedBit] = useState('0');
  const [hasFacility, setHasFacility] = useState(false);
  const [facilityLevel, setFacilityLevel] = useState(0);
  const [facilityPower, setFacilityPower] = useState(0);
  const [spacesLeft, setSpacesLeft] = useState(0);
  const [gigawattsAvailable, setGigawattsAvailable] = useState(0);
  const [miningRate, setMiningRate] = useState('0');
  const [hashRate, setHashRate] = useState('0');
  const [blocksUntilHalving, setBlocksUntilHalving] = useState('0');
  const [networkHashRatePercentage, setNetworkHashRatePercentage] = useState('0');
  const [totalNetworkHashRate, setTotalNetworkHashRate] = useState('0');
  
  // Contract read hooks
  const { data: facilityData } = useReadContract({
    address: MAIN_CONTRACT_ADDRESS,
    abi: MAIN_CONTRACT_ABI,
    functionName: 'facilities',
    args: [address],
    enabled: isConnected && !!address,
  });
  
  const { data: apeBalanceData } = useReadContract({
    address: BITAPE_TOKEN_ADDRESS,
    abi: BITAPE_TOKEN_ABI,
    functionName: 'balanceOf',
    args: [address],
    enabled: isConnected && !!address,
  });
  
  const { data: totalPowerData } = useReadContract({
    address: MAIN_CONTRACT_ADDRESS,
    abi: MAIN_CONTRACT_ABI,
    functionName: 'totalPower',
    enabled: isConnected,
  });
  
  const { data: rewardPerBlockData } = useReadContract({
    address: MAIN_CONTRACT_ADDRESS,
    abi: MAIN_CONTRACT_ABI,
    functionName: 'rewardPerBlock',
    enabled: isConnected,
  });
  
  // Contract write hooks
  const { writeContractAsync: writeMainContract, isPending: isMainContractPending } = useWriteContract();
  
  // Loading states
  const [isPurchasingFacility, setIsPurchasingFacility] = useState(false);
  const [isGettingStarterMiner, setIsGettingStarterMiner] = useState(false);
  const [isClaimingReward, setIsClaimingReward] = useState(false);
  const [isUpgradingFacility, setIsUpgradingFacility] = useState(false);
  
  // Update state based on contract data
  useEffect(() => {
    if (facilityData) {
      const [power, level, lastClaimBlock] = facilityData as [bigint, bigint, bigint];
      setFacilityPower(Number(power));
      setFacilityLevel(Number(level));
      setHasFacility(Number(power) > 0);
      
      // Calculate available gigawatts based on facility power
      setGigawattsAvailable(Number(power) / 1000); // Assuming 1 GW = 1000 power units
      
      // Calculate spaces left (placeholder logic)
      setSpacesLeft(0); // In BitApe, users have one facility
      
      // Calculate mining rate if facility exists
      if (Number(power) > 0 && totalPowerData && rewardPerBlockData) {
        const playerPower = Number(power);
        const totalPower = Number(totalPowerData);
        const rewardPerBlock = Number(rewardPerBlockData);
        
        // Calculate mining rate (BIT per day)
        const blocksPerDay = 7200; // Approximate blocks per day
        const dailyReward = (playerPower / totalPower) * rewardPerBlock * blocksPerDay;
        setMiningRate(dailyReward.toFixed(2));
        
        // Set hash rate (GH/s)
        setHashRate((playerPower * 228).toFixed(0)); // Simplified calculation
        
        // Network hash rate percentage
        const percentage = (playerPower / totalPower) * 100;
        setNetworkHashRatePercentage(percentage.toFixed(6));
        
        // Total network hash rate
        setTotalNetworkHashRate((totalPower * 228).toFixed(0));
        
        // Blocks until halving (placeholder)
        setBlocksUntilHalving('3,284,172');
        
        // Calculate mined BIT (placeholder)
        setMinedBit('0.5107');
      }
    }
  }, [facilityData, totalPowerData, rewardPerBlockData]);
  
  // Update APE balance
  useEffect(() => {
    if (isConnected && address) {
      // For demo purposes, set a placeholder APE balance
      setApeBalance('0.00818');
    }
  }, [isConnected, address]);
  
  // Update BIT balance
  useEffect(() => {
    if (apeBalanceData) {
      setBitBalance(formatEther(apeBalanceData as bigint));
    } else {
      // For demo purposes, set a placeholder BIT balance
      setBitBalance('26.5474');
    }
  }, [apeBalanceData]);
  
  // Action functions
  const purchaseFacility = async (referrer?: string) => {
    if (!isConnected || !address) return;
    
    try {
      setIsPurchasingFacility(true);
      
      await writeMainContract({
        address: MAIN_CONTRACT_ADDRESS,
        abi: MAIN_CONTRACT_ABI,
        functionName: 'purchaseInitialFacility',
        args: [referrer || '0x0000000000000000000000000000000000000000'],
        value: parseEther('10.003'), // Initial facility price + fees
      });
      
      // Refresh data after purchase
      // This would typically be handled by listening for events
      
    } catch (error) {
      console.error('Error purchasing facility:', error);
    } finally {
      setIsPurchasingFacility(false);
    }
  };
  
  const getStarterMiner = async () => {
    if (!isConnected || !address || !hasFacility) return;
    
    try {
      setIsGettingStarterMiner(true);
      
      await writeMainContract({
        address: MAIN_CONTRACT_ADDRESS,
        abi: MAIN_CONTRACT_ABI,
        functionName: 'getStarterMiner',
      });
      
      // Refresh data after getting starter miner
      
    } catch (error) {
      console.error('Error getting starter miner:', error);
    } finally {
      setIsGettingStarterMiner(false);
    }
  };
  
  const claimReward = async () => {
    if (!isConnected || !address || !hasFacility) return;
    
    try {
      setIsClaimingReward(true);
      
      await writeMainContract({
        address: MAIN_CONTRACT_ADDRESS,
        abi: MAIN_CONTRACT_ABI,
        functionName: 'claimReward',
      });
      
      // Reset mined BIT after claiming
      setMinedBit('0');
      
    } catch (error) {
      console.error('Error claiming reward:', error);
    } finally {
      setIsClaimingReward(false);
    }
  };
  
  const upgradeFacility = async () => {
    if (!isConnected || !address || !hasFacility) return;
    
    try {
      setIsUpgradingFacility(true);
      
      await writeMainContract({
        address: MAIN_CONTRACT_ADDRESS,
        abi: MAIN_CONTRACT_ABI,
        functionName: 'upgradeFacility',
      });
      
      // Refresh facility data after upgrade
      
    } catch (error) {
      console.error('Error upgrading facility:', error);
    } finally {
      setIsUpgradingFacility(false);
    }
  };
  
  return {
    isConnected,
    address,
    apeBalance,
    bitBalance,
    spacesLeft,
    gigawattsAvailable,
    miningRate,
    hashRate,
    blocksUntilHalving,
    networkHashRatePercentage,
    totalNetworkHashRate,
    minedBit,
    hasFacility,
    facilityLevel,
    facilityPower,
    purchaseFacility,
    getStarterMiner,
    claimReward,
    upgradeFacility,
    isPurchasingFacility,
    isGettingStarterMiner,
    isClaimingReward,
    isUpgradingFacility,
  };
}
