import React from 'react';

interface RoomVisualizationProps {
  hasFacility: boolean;
  onPurchaseFacility: () => Promise<void>;
  onGetStarterMiner: () => Promise<void>;
  onUpgradeFacility: () => Promise<void>;
  isPurchasingFacility: boolean;
  isGettingStarterMiner: boolean;
  isUpgradingFacility: boolean;
}

const RoomVisualization: React.FC<RoomVisualizationProps> = ({
  hasFacility,
  onPurchaseFacility,
  onGetStarterMiner,
  onUpgradeFacility,
  isPurchasingFacility,
  isGettingStarterMiner,
  isUpgradingFacility
}) => {
  if (!hasFacility) {
    return (
      <div className="room-container flex flex-col items-center justify-center">
        <h2 className="pixel-text text-2xl mb-8">YOU DONT HAVE A SPACE TO MINE!</h2>
        <button 
          className="pixel-button"
          onClick={onPurchaseFacility}
          disabled={isPurchasingFacility}
        >
          {isPurchasingFacility ? 'PURCHASING...' : 'BUY FACILITY'}
        </button>
      </div>
    );
  }

  return (
    <div className="room-container relative">
      {/* This would be replaced with actual room visualization with miners */}
      <div className="absolute inset-0 flex items-center justify-center">
        <div className="grid grid-cols-3 gap-4 p-4">
          {/* Placeholder for miners */}
          <div className="miner bg-royal-blue-dark p-2 border border-banana-yellow">
            <div className="w-12 h-12 bg-banana-yellow-dark"></div>
          </div>
          <div className="miner bg-royal-blue-dark p-2 border border-banana-yellow">
            <div className="w-12 h-12 bg-banana-yellow-dark"></div>
          </div>
          <div className="miner bg-royal-blue-dark p-2 border border-banana-yellow">
            <div className="w-12 h-12 bg-banana-yellow-dark"></div>
          </div>
        </div>
      </div>
      
      {/* Room furniture (simplified) */}
      <div className="absolute bottom-4 right-4 w-16 h-24 bg-royal-blue-light border border-banana-yellow"></div>
      <div className="absolute top-4 right-4 w-24 h-16 bg-royal-blue-light border border-banana-yellow"></div>
      
      {/* Control buttons */}
      <div className="absolute bottom-4 left-4 flex space-x-2">
        <button 
          className="pixel-button text-xs"
          onClick={onGetStarterMiner}
          disabled={isGettingStarterMiner}
        >
          GRID
        </button>
        <button 
          className="pixel-button text-xs"
          onClick={onUpgradeFacility}
          disabled={isUpgradingFacility}
        >
          UPGRADE
        </button>
      </div>
      
      {/* BIGBOY 9000 label */}
      <div className="absolute bottom-4 right-4">
        <span className="pixel-text text-banana-yellow text-xs">BITBOY 9000</span>
      </div>
    </div>
  );
};

export default RoomVisualization;
